// pages/jobs/[id].js
import { useRouter } from "next/router";
import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import { ArrowLeft, Briefcase, MapPin, DollarSign, Clock } from "lucide-react";
import Link from "next/link";

export default function JobDetails() {
  const router = useRouter();
  const { id } = router.query;
  const [job, setJob] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  useEffect(() => {
    if (!id) return;

    async function fetchJob() {
      try {
        const res = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/api/jobs/${id}`);
        if (!res.ok) throw new Error(`HTTP ${res.status}`);
        const data = await res.json();
        setJob(data.job);
      } catch (err) {
        console.error("Error fetching job:", err);
        setError("Failed to load job details.");
      } finally {
        setLoading(false);
      }
    }

    fetchJob();
  }, [id]);

  if (loading) {
    return (
      <div className="flex justify-center items-center min-h-screen text-orange-600 text-lg">
        Loading job details...
      </div>
    );
  }

  if (error || !job) {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen text-gray-600">
        <p className="text-lg mb-3">{error || "Job not found."}</p>
        <Link href="/jobs" className="text-orange-600 hover:text-orange-700 font-medium">
          ‚Üê Back to Jobs
        </Link>
      </div>
    );
  }

  return (
    <motion.div
      className="min-h-screen bg-gradient-to-b from-orange-50 to-white py-16 px-6"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <div className="max-w-3xl mx-auto bg-white border border-orange-100 rounded-2xl shadow-md p-8">
        <Link
          href="/jobs"
          className="flex items-center text-orange-600 hover:text-orange-700 mb-6"
        >
          <ArrowLeft className="w-4 h-4 mr-1" /> Back to Jobs
        </Link>

        <h1 className="text-3xl font-bold text-orange-700 mb-4 flex items-center gap-2">
          <Briefcase className="w-6 h-6 text-orange-500" /> {job.service}
        </h1>

        <p className="text-gray-700 mb-6 text-lg leading-relaxed">{job.description}</p>

        <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 text-gray-600">
          <div className="flex items-center gap-2">
            <MapPin className="w-5 h-5 text-orange-400" /> {job.district}
          </div>

          <div className="flex items-center gap-2">
            <DollarSign className="w-5 h-5 text-orange-400" />{" "}
            {Number(job.budget).toFixed(2)} USD
          </div>

          <div className="flex items-center gap-2">
            <Clock className="w-5 h-5 text-orange-400" />{" "}
            {new Date(job.created_at).toLocaleString()}
          </div>

          <div>
            <span
              className={`inline-block px-3 py-1 text-sm rounded-full ${
                job.status === "pending"
                  ? "bg-yellow-100 text-yellow-700"
                  : "bg-green-100 text-green-700"
              }`}
            >
              {job.status}
            </span>
          </div>
        </div>

        <div className="mt-8 flex justify-end">
          <button
            onClick={() => alert("Feature coming soon: Apply or Contact")}
            className="bg-orange-500 hover:bg-orange-600 text-white px-5 py-2 rounded-xl font-medium shadow-md transition"
          >
            Apply for this Job
          </button>
        </div>
      </div>
    </motion.div>
  );
}
